import app from "./application"
import echo from './echo'
import updateSidebar from "./ui/sidebar"
import createTabs from "./ui/tabs";
import keepFocused from "./ui/form/helpers";
import globalSearch from "./ui/header/global-search";
import notifications, { NotificationBuilder, NotificationActionBuilder } from "./ui/notifications"

import createDiff from "./ui/diff";
import Popup from "./ui/popup/popup";
import SimpleJSONContentBuilder from "./simpleJSONContentBuilder";
import lang from "./lang";
import steps from "./ui/steps";
import {select, registerCreateSelectItem} from "./ui/select"
import ajaxCreateDepartmentOptions from "./select/ajax/department"
import ajaxCreateUserOption from "./select/ajax/users"
import ajaxCreateNomenclatureOption from "./select/ajax/nomenclatures"
import ajaxCreateUnitOption from "./select/ajax/units"
import ajaxCreateProcedureOption from "./select/ajax/procedure"
import ajaxCreateContractorOption from "./select/ajax/contractor"
import ajaxCreateSetOption from "./select/ajax/set"

import router from "./router";
import laroute = require("/js/laroute.js");

(function () {

    registerCreateSelectItem('/api/search/department', ajaxCreateDepartmentOptions);
    registerCreateSelectItem('/api/search/users', ajaxCreateUserOption);
    registerCreateSelectItem('/api/search/nomenclatures', ajaxCreateNomenclatureOption);
    registerCreateSelectItem('/api/search/units', ajaxCreateUnitOption);
    registerCreateSelectItem('/api/search/procedures', ajaxCreateProcedureOption);
    registerCreateSelectItem('/api/search/contractors', ajaxCreateContractorOption);
    registerCreateSelectItem(laroute.route('search.sets', { department: 0 }), ajaxCreateSetOption);

    app
        .addOnFirstLoadedEvent(echo.connect.bind(echo))
        .addOnLoadEvent(updateSidebar)
        .addOnLoadEvent(select)
        .addOnLoadEvent(createTabs)
        .addOnLoadEvent(keepFocused)
        .addOnLoadEvent(globalSearch)
        .addOnLoadEvent(createDiff)
        .addOnLoadEvent(steps)
        .addOnLoadEvent(router);

    app.addEmmitable('select', select);

    const fixLoading = [
        Popup, notifications
    ];

    // const a = new Popup('/_form.json', 'Регистрация', 'Регистрация нового пациента');
    // a.jsonContentBuilder = new SimpleJSONContentBuilder();
    // a.isForm = true;
    // a.create();
    // a.show();

    app.callOnLoadedEvents();
})();
